import axios from "axios";
import { useEffect } from "react";
import { useState } from "react";
import styled from "styled-components";
import { filterByCode } from "../config";

const Wrapper = styled.section`
margin-top: 3rem;
width: 100%;
display: grid;
grid-template-columns: 100%;
gap: 2rem;

@media (min-width: 767px) {
grid-template-columns: minmax(100px, 400px) 1fr;
align-items: center;
gap: 5 rep;
}

@media (min-width: 1024px) {
  grid-template-columns: minmax(400px, 600px) 1fr;
}
`;
const InfoImage = styled.img`
display: block;
width: 100%;
height: 100%;
object-fit: contain;
`;
const InfoTittle = styled.h1`
margin: 0;
font-weight: var(--fw-normal);
`;
const ListGroup = styled.div`
display: flex;
flex-direction: column;

gap: 2rem;

@media (min-width: 1024px) {
  flex-direction: row;
  gap: 4rem;
}
`;
const List = styled.ul`
list-style: none;
margin: 0;
padding: 0;
`;
const ListItem = styled.li`
line-height: 1.8rem;

& > b {
  font-weight: var(--fw-bold);
}
`;

const Meta = styled.div`
margin-top: rem;
display: flex;
gap: 1.5rem;
flex-direction: column;
align-items: flex-start;

& > b {
  font-weight: var(--fw-bold);
}

@media (min-width: 767px) {
flex-direction: row;
align-items: center;
}
`;

const TagGroup = styled.div`
display: flex;
gap: 1rem;
flex-wrap: wrap;
`;

const Tag = styled.span`
padding: 0 1rem;
background-color: var(--color-ui-base);
box-shadow: var(--shadow);
line-height: 1.5;
cursor: pointer;
color: var(--color-text);

:hover {
 background-color: var(--color-hover);
 color: black;
}
`;

const Info = (props) => {
  const {
    name,
    nativeName,
    flag,
    capital,
    population,
    region,
    subregion,
    topLevelDomain,
    currencies = [],
    languages = [],
    borders = [],
    push,
  } = props

  const [neighbors, setNeighbors] = useState([])

  useEffect(() => {
    if (borders.length)
      axios.get(filterByCode(borders)).then(({ data }) => setNeighbors(data.map(el => el.name)))
  }, [borders])


  return (
    <Wrapper>
      <InfoImage src={flag} alt={name} />

      <div>
        <InfoTittle>{name}</InfoTittle>
        <ListGroup>
          <List>
            <ListItem>
              <b>Native name:</b> {nativeName}
            </ListItem>
            <ListItem>
              <b>Population:</b> {population}
            </ListItem>
            <ListItem>
              <b>Region:</b> {region}
            </ListItem>
            <ListItem>
              <b>Sub Region:</b> {subregion}
            </ListItem>
            <ListItem>
              <b>Capital:</b> {capital}
            </ListItem>
          </List>
          <List>
            <ListItem>
              <b>Top Level domain</b>{' '}
              {topLevelDomain.map(el => <span key={el}>{el}</span>)}
            </ListItem>
            <ListItem>
              <b>Currency</b>{' '}
              {currencies.map(el => <span key={el.code}>{el.name}</span>)}
            </ListItem>
            <ListItem>
              <b>Languages</b>{' '}
              {languages.map(el => <span key={el.name}>{el.name} </span>)}
            </ListItem>
          </List>
        </ListGroup>
        <Meta>
          <b>Border Countries</b>
          {!borders.length ? (
            <span>There is no border countries</span>
          ) : (
            <TagGroup>
              {neighbors.map(el => <Tag key={el} onClick={() => push(`/country/${el}`)}>{el}</Tag>)}
            </TagGroup>
          )}
        </Meta>
      </div>
    </Wrapper>
  )
}
export default Info